*{
  box-sizing: border-box;
}
.parent{
  min-height: 100vh;
  margin-top: 0;
  padding: 20px 40px ;
  padding-bottom: 100px;
  font-weight: 500;
}
.header{
  width: 70%;
  height:70px;
  margin:0  auto;
  border-radius: 20px;
  padding: 20px 20px;
  
}
.header .searchbox{
  background-color: white;
  border-radius: 10px;
  display: grid;
  grid-template-columns: 50px auto;
  align-items: center;
  font-size: 1.2rem;
  padding: 5px 10px;
}
.header .searchbox .text{
  border: none;
  outline: none;
  font-weight: 500;
  font-size: 1.4rem;
}


/* section */
.section{
  width: 70%;
  padding: 20px 0px;
  margin: 20px auto;
  margin-bottom: 0;
  border-radius: 20px;
  /* background: linear-gradient(rgb(5, 131, 173),rgb(150, 185, 197)); */
}
.section .section-one{
  box-shadow: 0px 0px 5px black;
  border-radius: 20px;
  margin :0 20px;
  padding: 20px 20px;
  display: grid;
  grid-template-columns: 67% 33%;
  grid-gap:20px;
  background-color: aliceblue;
  align-items: center;
}
.section .section-one .date-time{
  font-size: 1.4rem;
  text-shadow: 0px 0px 2px gray;
  margin: 0;
}
.section .section-one .city-country{
  font-size: 1.4rem;
  text-shadow: 0px 0px 2px gray;
}
.section .section-two{
  padding :20px 20px;
  padding-bottom: 0;
  grid-gap:20px;
  display: grid;
  grid-template-columns:repeat(3,1fr) ;
  grid-template-rows: repeat(3,1fr);
}
.section .section-two div{
  /* box-shadow: 0px 0px 5px black;
  border-radius: 20px; */
  text-align: center;
  font-size: 1.4rem;
  text-shadow: 0px 0px 2px gray;
}
.section .section-two div p{
  padding: 0;
  margin:5px 0;
}
.section .section-two div div{
  margin-top:10px ;
  box-shadow: none;
  display: grid;
  align-items: center;
  text-align: center;
  padding: 0;
  margin: 0;
  font-size: 1.4rem;
  text-shadow: 0px 0px 2px gray;
  justify-items: center;
  grid-template-columns: repeat(1,1fr);
}
.section .section-two div .iconss{
  text-align: right;
  font-size: 2.6rem;
  text-shadow: 0px 0px 2px gray;
}
.section .section-two .type{
  background-size: contain;
  display: grid;
  grid-template-columns: 1fr;
  align-items:end;
  justify-items: center;
}
.hide{
  font-weight: bold;
  width: 70%;
  padding: 20px 0px;
  margin: 0  auto;
  border-radius: 20px;

}
.hide .hide-two{
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4,1fr);
  grid-gap:20px;
  justify-items: center;
  align-items: center;
  font-size: 1.4rem;
}
.hide .hidebutton{
  font-weight: bold;
  outline: none;
  padding: 10px;
  border: none;
  border-radius: 10px;
  font-size: 1.4rem;
  display: block;
  width:40%;
  margin:auto;
}
.hide .hidebutton:hover{
  opacity: 0.8;
  transform: scale(1.1);
}
@media (max-width:1300px) {
  .header .searchbox .text{
    font-size: 1.3rem;
  }
  .section .section-two{
    grid-template-columns: repeat(2,1fr);
    grid-gap: 10px;
  }
  .section .section-two .type{
    grid-area: 1/1/2/3;
  }
  .section .section-two div{
    font-size: 1.3rem;
  }
  .section .section-two div div{
    font-size: 1.3rem;
  }
  .section .section-two div .iconss{
    justify-self: center;
    font-size:2.2rem;
  }
  .section .section-one{
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
  }
  .section .section-one .city-country{
    font-size: 1.3rem;
  }
  .section .section-one .date-time{
    font-size: 1.3rem;
  }
  .hide .hide-two{
    grid-template-columns: repeat(2,1fr);
    font-size: 1.3rem;
  }
  .hide .hidebutton{
    width: auto;
    font-size: 1.3rem;
  }
}

@media (max-width:500px) {
  .parent{
    padding: 20px 20px;
  }
  .header{
    width: 100%;

  }
  .header .searchbox{
    grid-template-columns: 10% 90%;
    grid-gap:5px;
  }
  .section{
    width: 100%;
  }
  .section .section-one{
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    grid-gap:0px;
    margin-bottom: 20px;
  }
  .section .section-one .city-country{
    font-size: 1rem;
  }
  .section .section-one .date-time{
    font-size: 1rem;
  }
  .section .section-two{
    padding: 5px 5px;
    grid-gap :5px;
    grid-template-columns: repeat(2,1fr);
  }
  .section .section-two div{
    font-size: 1rem;
  }
  .section .section-two div div{
    justify-items: center;
    font-size: 1rem;
  }
  .section .section-two div .iconss{
    justify-self: center;
    font-size:1.4rem;
  }
  .hide .hide-two{
    grid-template-columns: repeat(1,1fr);
    font-size: 1rem;
  }
  .hide .hidebutton{
    width: auto;
    font-size: 0.8rem;
  }
}
